const advSlider = function () {
  const slider = window.document.querySelector('.slider-adv');
  const sliderContent = window.document.querySelector('.slider-adv__content');
  const sliderItems = window.document.querySelectorAll('.slider-adv__item');
  const sliderItem = window.document.querySelector('.slider-adv__item');
  const sliderItemTexts = window.document.querySelectorAll(
    '.slider-adv__item__text'
  );
  const sliderNavBtnPrev = window.document.querySelector(
    '.slider-adv__nav__btn-prev'
  );
  const sliderNavBtnNext = window.document.querySelector(
    '.slider-adv__nav__btn-next'
  );

  const slidesNum = sliderItems.length;
  let curSlide = 0;

  function goToSlide(slideNum) {
    sliderItems.forEach((slide, i) => {
      slide.style.transform = `translateX(${100 * (i - slideNum)}%)`;
    });
  }

  function nextSlide() {
    curSlide += 1;
    if (curSlide === slidesNum) {
      curSlide = 0;
    }
    goToSlide(curSlide);
  }

  function prevSlide() {
    curSlide -= 1;
    if (curSlide < 0) {
      curSlide = slidesNum - 1;
    }
    goToSlide(curSlide);
  }

  let timerId;
  function startAutoSlide() {
    timerId = window.setTimeout(() => {
      nextSlide();
      startAutoSlide();
    }, 3000);
  }

  function endAutoSlide() {
    window.clearTimeout(timerId);
  }

  //   function resetAutoSlide() {
  //     endAutoSlide();
  //     startAutoSlide();
  //   }

  slider.addEventListener('mouseenter', endAutoSlide);
  slider.addEventListener('mouseleave', startAutoSlide);

  const sliderInit = function () {
    goToSlide(0);
    startAutoSlide();
  };

  sliderNavBtnPrev.addEventListener('click', prevSlide);
  sliderNavBtnNext.addEventListener('click', nextSlide);

  // document.addEventListener('keyup', (e) => {
  //   window.console.log(e.key);
  //   switch (e.key) {
  //     case 'ArrowLeft':
  //       prevSlide();
  //       break;
  //     case 'ArrowRight':
  //       nextSlide();
  //       break;
  //     default:
  //       break;
  //   }
  // });

  sliderInit();

  //

  let isDown = false;
  let isDrag = false;
  let startX = 0;
  let endX = 0;

  slider.addEventListener('mousedown', (e) => {
    isDown = true;
    slider.classList.add('slider--active');
    startX = e.pageX;
  });

  slider.addEventListener('mouseleave', () => {
    isDown = false;
  });

  slider.addEventListener('mouseup', () => {
    isDown = false;

    if (isDrag === true) {
      if (endX - startX > 0) {
        prevSlide();
      } else {
        nextSlide();
      }
      isDrag = false;
    }
  });

  slider.addEventListener('mousemove', (e) => {
    if (!isDown) return;
    e.preventDefault();
    isDrag = true;
    endX = e.pageX;
  });

  //
};

export default advSlider;
