const mainSlider = function () {
  const slider = window.document.querySelector('.main-slider');
  const sliderContent = window.document.querySelector('.main-slider__content');
  const sliderItems = window.document.querySelectorAll('.main-slider__item');
  const sliderItem = window.document.querySelector('.main-slider__item');
  const sliderItemTexts = window.document.querySelectorAll(
    '.main-slider__item__text'
  );
  const sliderNavBtnPrev = window.document.querySelector(
    '.main-slider__nav__btn-prev'
  );
  const sliderNavBtnNext = window.document.querySelector(
    '.main-slider__nav__btn-next'
  );
  const sliderIndicators = window.document.querySelector(
    '.main-slider__indicators'
  );

  let sliderIndicatorsCurrentNumber;

  let sliderIndicatorsItems;

  const slidesNum = sliderItems.length;
  let curSlide = 0;

  function goToSlide(slideNum) {
    sliderItems.forEach((slide, i) => {
      slide.style.transform = `translateX(${100 * (i - slideNum)}%)`;
    });
    activeDot(slideNum);
    // resetAutoSlide();
    // resetInterval();
  }

  const nextSlide = function () {
    curSlide++;
    if (curSlide === slidesNum) {
      curSlide = 0;
    }
    goToSlide(curSlide);
  };

  const prevSlide = function () {
    curSlide--;
    if (curSlide < 0) {
      curSlide = slidesNum - 1;
    }
    goToSlide(curSlide);
  };

  //   let interval = window.setInterval(nextSlide, 3000);
  //   const resetInterval = function () {
  //     window.clearInterval(interval);
  //     interval = window.setInterval(nextSlide, 3000);
  //   };

  let timerId;
  function startAutoSlide() {
    timerId = window.setTimeout(() => {
      nextSlide();
      startAutoSlide();
    }, 3000);
  }

  function endAutoSlide() {
    window.clearTimeout(timerId);
  }

  slider.addEventListener('mouseenter', endAutoSlide);
  slider.addEventListener('mouseleave', startAutoSlide);

  function createDots() {
    sliderIndicators.insertAdjacentHTML(
      'beforeend',
      `<span class="main-slider__indicators__number main-slider__indicators__number--currentNumber">00</span>`
    );

    sliderItems.forEach((_, i) => {
      sliderIndicators.insertAdjacentHTML(
        'beforeend',
        `<button class="main-slider__indicators__item"  data-slide="${i}"></button>`
      );
    });

    sliderIndicatorsItems = sliderIndicators.querySelectorAll(
      '.main-slider__indicators__item'
    );

    sliderIndicators.insertAdjacentHTML(
      'beforeend',
      `<span class="main-slider__indicators__number main-slider__indicators__number--totalNumber">0${sliderIndicatorsItems.length}</span>`
    );

    sliderIndicatorsCurrentNumber = window.document.querySelector(
      '.main-slider__indicators__number--currentNumber'
    );
  }

  function activeDot(slideNum) {
    sliderIndicatorsItems.forEach((slide) => {
      slide.classList.remove('main-slider__indicators__item--active');
    });

    sliderIndicatorsCurrentNumber.innerHTML = `0${+slideNum + 1}`;

    sliderIndicators
      .querySelector(`[data-slide='${slideNum}']`)
      .classList.add('main-slider__indicators__item--active');
  }

  const sliderInit = function () {
    createDots();
    goToSlide(0);
    startAutoSlide();
  };

  sliderNavBtnPrev.addEventListener('click', prevSlide);
  sliderNavBtnNext.addEventListener('click', nextSlide);

  // document.addEventListener('keyup', (e) => {
  //   window.console.log(e.key);
  //   switch (e.key) {
  //     case 'ArrowLeft':
  //       prevSlide();
  //       break;
  //     case 'ArrowRight':
  //       nextSlide();
  //       break;
  //     default:
  //       break;
  //   }
  // });

  sliderIndicators.addEventListener('click', (e) => {
    if (e.target.classList.contains('main-slider__indicators__item')) {
      const { slide } = e.target.dataset;
      goToSlide(slide);
    }
  });

  sliderInit();

  //

  let isDown = false;
  let isDrag = false;
  let startX = 0;
  let endX = 0;

  slider.addEventListener('mousedown', (e) => {
    isDown = true;
    slider.classList.add('slider--active');
    startX = e.pageX;
  });

  slider.addEventListener('mouseleave', () => {
    isDown = false;
    // slider.classList.remove("slider--active");
  });

  slider.addEventListener('mouseup', () => {
    isDown = false;
    // slider.classList.remove("slider--active");

    if (isDrag == true) {
      if (endX - startX > 0) {
        prevSlide();
      } else {
        nextSlide();
      }
      isDrag = false;
    }
  });

  slider.addEventListener('mousemove', (e) => {
    if (!isDown) return;
    e.preventDefault();
    // slider.classList.add("slider--active");
    isDrag = true;
    endX = e.pageX;
  });

  //
};

export default mainSlider;
