const taggedHeaderList = window.document.querySelector('.tagged__header-list');

let taggedHeaderItem = window.document.querySelector('.tagged__header__item');

const tabMenInit = () => {
  // #region To handle tagged actions
  taggedHeaderList.addEventListener('click', (e) => {
    if (e.target.classList.contains('tagged__header__item')) {
      // window.console.log(e.target.dataset.category);

      // window.console.log(taggedHeaderItem);
      window.document
        .querySelector(`.${taggedHeaderItem.dataset.category}`)
        .classList.add('u-hidden');
      taggedHeaderItem.classList.remove('active');

      taggedHeaderItem = e.target;
      // window.console.log(taggedHeaderItem);
      taggedHeaderItem.classList.add('active');
      window.document
        .querySelector(`.${taggedHeaderItem.dataset.category}`)
        .classList.remove('u-hidden');
    }
  });
  // #endregion
};

export default tabMenInit;
