import mainSlider from './main-slider';
import advSlider from './adv-slider';
import tabMenInit from './tabMenu';
import InternetToastNotificationInit from './internetToastNotification';
import modalInit from './modal';
import scrollToTopBtnInit from './scroll-to-top-button';
import smoothscrollingInit from './smooth-scrolling';
import updateCurrentYear from './update-current-year';

advSlider();
mainSlider();
tabMenInit();
InternetToastNotificationInit();
modalInit();
scrollToTopBtnInit();
smoothscrollingInit();
