const toast = window.document.querySelector('.toast');
const toastIcon = window.document.querySelector('.toast__icon');
const toastText = window.document.querySelector('.toast__text');
const toastCloseBtn = window.document.querySelector('.toast__close-btn');

function createToast(status) {
  if (status === true) {
    toastIcon.innerHTML = `
    <i class="fas fa-wifi fa-stack-1x" style="color: rgb(0, 162, 0)"></i>
    `;
    toastText.innerHTML = 'You are internet connecction was restored.';
  } else {
    toastIcon.innerHTML = `<i class="fas fa-wifi fa-stack-1x" style="color: #d9dce1"></i>
                           <i class="fas fa-ban fa-stack-2x" style="color: Tomato"></i>`;

    toastText.innerHTML = `You are currently offline.<br />
                           Check your internet connecction.`;
  }
}

function showToast() {
  toast.style.zIndex = '99999';
  window.setTimeout(() => {
    toast.style.opacity = '1';
  }, 300);
}

function hideToast() {
  toast.style.opacity = '0';
  window.setTimeout(() => {
    toast.style.zIndex = '-1';
  }, 300);
}

const InternetToastNotificationInit = () => {
  toastCloseBtn.addEventListener('click', hideToast);

  let connectionStatus = true;
  let currentStatus;

  window.setInterval(() => {
    currentStatus = window.navigator.onLine;

    if (currentStatus !== connectionStatus) {
      window.console.log(currentStatus);

      createToast(currentStatus);
      showToast();
      window.setTimeout(hideToast, 5000);

      connectionStatus = currentStatus;
    }
  }, 5000);
};

export default InternetToastNotificationInit;
